body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


header {
  font-size: .75rem;
  text-align: center;
  background-color: rgb(244,103,23);
  border-radius: 1rem;
  box-shadow: 0rem 0rem 5rem rgb(233,215,206);
}

img {
  width: 100%;
  height: 100%;
}

/* Class */

.flip-to-back {
  -webkit-transform: rotateY(180deg);
          transform: rotateY(180deg);
  -webkit-transition: -webkit-transform 0.8s;
  transition: -webkit-transform 0.8s;
  transition: transform 0.8s;
  transition: transform 0.8s, -webkit-transform 0.8s;
}

.flip-card {
  background-color: transparent;
  width: 163px;
  height: 195px;
  margin: 5px;
  box-shadow: 0rem 0rem 5rem 0.5rem rgb(233,215,206);
}

.flip-card-inner {
  position: relative;
  width: 100%;
  height: 100%;
  text-align: center;
  -webkit-transition: -webkit-transform 0.8s;
  transition: -webkit-transform 0.8s;
  transition: transform 0.8s;
  transition: transform 0.8s, -webkit-transform 0.8s;
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
}

.flip-card-front, .flip-card-back {
  position: absolute;
  width: 100%;
  height: 100%;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
  /* -moz-backface-visibility: hidden; */
  -webkit-transform: rotateX(0deg);
          transform: rotateX(0deg);
}

.flip-card-front {
  -webkit-transform: rotateY(180deg);
          transform: rotateY(180deg);
}

.modal-hide {
  visibility: hidden;
}

/* ID */

#card-container {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  width: 709px;
  padding: 5px;
}

#explosion {
  width: 93%;
  height: 93%;
}

#game-container {
  background-image: url(/Clicky-Game/static/media/the_expanse.aebb584c.jpg);
  background-size: 100% 100%;
  width: 100vw;
  height: 100vh;
  display: grid;
  grid-template-rows: 1fr 1fr 1fr 1fr 4fr;
  -webkit-justify-content: center;
          justify-content: center;
}

#instruction-list {
  text-align: left;
}

#instructions-opening {
  font-size: 2rem;
  text-align: center;
}

#modal-bg {
  position: fixed;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  background-color: rgba(39, 38, 38, 0.5);
  height: 100vh;
  width: 100vw;
  z-index: 1;
}

#modal {
  width: 75vw;
  height: 75vh;
  color: rgb(233,215,206);
  background-color: rgb(88,47,52);
  border-radius: 1rem;
  box-shadow: 0rem 0rem 5rem .5rem rgb(233,215,206);
  text-align: center;
  padding: 5px;
}

#score {
  font-size: .75rem;
  text-align: center;
  background-color: rgb(244,103,23);
  border-radius: 1rem;
  margin: 10px 0px;
  box-shadow: 0rem 0rem 5rem rgb(233,215,206);
}

#start {
  text-align: center;
}

#win {
  width: 70%;
}

@media screen and (max-width: 850px) {
  #instructions-opening {
    font-size: 1.5rem;
  }
}

@media screen and (max-width: 725px) {
  :root {
    font-size: 10px;
  }
  #card-container {
    margin-left: 5%;
  }
  #game-container {
    background-image: none;
    background-color: rgb(0,74,92);
    height: 100vh;
    width: 100vw;
  }
  .flip-card {
    height: 150px;
    width: 125px;
  }
}

@media screen and (max-width: 680px) {
  html {
    width: 100%;
    height: 100%;
    padding: 0%;
    margin: 0%;
  }
  body {
    width: 100%;
    height: 100%;
  }
  #card-container {
    width: 100%;
    height: 100%;
  }
}

@media screen and (max-width: 575px) {
  .flip-card {
    height: 240px;
    width: 200px;
  }
  #card-container {
    width: 85%;
  }
}

@media screen and (max-width: 475px) {
  body {
    height: 300%;
  }
  html {
    height: 300%;
  }
  .flip-card {
    height: 350px;
    width: 292px;
  }
}
